import React from 'react';


import '../css/navbar.css';
import logo from '../assets/logo.jpg';

function Navbar(props) {
    return (
        <nav className="navbar navbar-expand-lg navbar-dark navbar-bg sticky-top">
            <div className="container navbar-container">
                <a className="navbar-brand" href="/"><img src={logo} alt="logo" className="navbar-logo mr-2" />Portfolio</a>
                <button className="navbar-toggler"
                    type="button"
                    data-toggle="collapse"
                    data-target="#navbarContent"
                    aria-controls="navbarContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarContent">
                    <ul className="navbar-nav mr-auto navbar-links-container">
                        <li className="nav-item">
                            <a className="nav-link text-white" href="/"><i class="fa fa-home mr-2" aria-hidden="true"></i>HOME</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link text-white" href="/about"><i class="fa fa-user-circle mr-2" aria-hidden="true"></i>ABOUT</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link text-white" href="/skills"><i class="fa fa-briefcase mr-2" aria-hidden="true"></i>SKILLS</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link text-white" href="/projects"><i class="fa fa-folder-open mr-2" aria-hidden="true"></i>PROJECTS</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link text-white" href="/contact"><i class="fa fa-address-card mr-2" aria-hidden="true"></i>CONTACT</a>
                        </li>
                    </ul>
                </div>

            </div>
        </nav>
    );
}

export default Navbar;