import React from 'react';

import SQLServerLogo from '../../assets/st/SQLServer.png';

function StorageTechnologies(props) {
    return (
        <div className="card-tech-container">
            <div class="card text-center card-tech">
                <img class="card-img-top" src={SQLServerLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Microsoft SQL Server<span className="text-muted ml-2"></span></p>
                </div>
            </div>
        </div>
    );
}

export default StorageTechnologies;