import React from 'react';

import '../../css/home.css';
import myimage from '../../assets/image.jpg';

function Home(props) {
    return (
        <div className="row banner-container shadow">
            <div className="banner shadow">
                <div className="row">
                    <div className="col-md-5">
                        <img src={myimage} alt="Me" className="banner-image" />
                    </div>
                    <div className="col-md-7 p-5">
                        <h5 className="home-greeting-text">HELLO, I AM</h5>
                        <h1 className="mb-3">NAVIN VARU</h1>
                        <h5 className="mb-4 home-profile-text">FULL STACK DEVELOPER</h5>
                        <p className="text-muted justify mb-5">Passionate to develop flexible, reusable and extensible software applications | Ever Learner | Quick Grasping</p>
                        <p className="text-muted"><span className="mr-3 text-warning"><i class="fa fa-birthday-cake" aria-hidden="true"></i></span>13th April, 1990<span></span></p>
                        <p className="text-muted"><span className="mr-3 text-primary"><i class="fa fa-phone" aria-hidden="true"></i></span><span>+91-8976749680, <span className="portfolio-badge">+91-9106379505</span></span></p>
                        <p className="text-muted"><span className="mr-3 text-info"><i class="fa fa-envelope" aria-hidden="true"></i></span><span>navin.varu31@gmail.com, <span className="portfolio-badge">navin.varu.er@gmail.com</span></span></p>
                        <p className="text-muted"><span className="mr-3 text-dark"><i class="fa fa-home" aria-hidden="true"></i></span><span>Pune, Maharashtra, India.</span></p>
                    </div>
                </div>

            </div>

        </div>
    );
}

export default Home;