import React from 'react';

import ASPNETLogo from '../../assets/sst/ASPNET.jpg';
import ASPNETCORELogo from '../../assets/sst/ASPNETCORE.png';
import EFCoreLogo from '../../assets/sst/EFCore.png';
import djangoLogo from '../../assets/sst/django.png';
import djangoRESTLogo from '../../assets/sst/djangoREST.png';
import ASPCORERESTLogo from '../../assets/sst/ASPCOREREST.png';

function ServerSideTechnologies(props) {
    return (
        <div className="card-tech-container">
            <div class="card text-center card-tech">
                <img class="card-img-top" src={ASPNETLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">ASP.NET<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={ASPNETCORELogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">ASP.NET Core<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={EFCoreLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Entity Framework Core<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={djangoLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Django<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={djangoRESTLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Django REST Framework<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={ASPCORERESTLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">ASP.NET Core REST API<span className="text-muted ml-2"></span></p>
                </div>
            </div>
        </div>
    );
}

export default ServerSideTechnologies;