import React from 'react';

import OnionLogo from '../../assets/arc/Onion.png';
import SOALogo from '../../assets/arc/SOA.png';
import LayeredLogo from '../../assets/arc/Layered.png';
import MVCLogo from '../../assets/arc/MVC.jpg';

function Architectures(props) {
    return (
        <div className="card-tech-container">
            <div class="card text-center card-tech">
                <img class="card-img-top" src={OnionLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Onion Architecture<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={LayeredLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Layered Architecture<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={SOALogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Service Oriented Architecture<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={MVCLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">MVC Architecture<span className="text-muted ml-2"></span></p>
                </div>
            </div>
        </div>
    );
}

export default Architectures;