import React from 'react';

import '../../css/skills.css';
import Summary from '../skills/summary';
import ProgrammingLanguages from '../skills/programmingLanguages';
import Architectures from '../skills/architectures';
import ClientSideTechnologies from '../skills/clientSideTechnologies';
import ServerSideTechnologies from '../skills/serverSideTechnologies';
import StorageTechnologies from '../skills/storageTechnologies';

function Skills(props) {
    return (
        <>
            <div className="row skills-banner-container shadow">
                <div className="skills-banner shadow">
                    <div className="row">
                        <div className="col-md-12">
                            <h4 className="text-muted"><span className="text-info mr-3"><i class="fa fa-list-ul" aria-hidden="true"></i></span>Summary</h4>
                            <hr className="mb-5" />
                            <Summary />
                        </div>
                    </div>
                </div>
            </div>
            <div className="row skills-lang-container">
                <div className="skills-lang shadow">
                    <div className="row">
                        <div className="col-md-12">
                            <h4 className="text-muted"><span className="text-primary mr-3"><i class="fa fa-file-code-o" aria-hidden="true"></i></span>Programming Languages</h4>
                            <hr className="mb-5" />
                            <ProgrammingLanguages />
                        </div>
                    </div>
                </div>
            </div>
            <div className="row skills-arc-container">
                <div className="skills-arc shadow">
                    <div className="row">
                        <div className="col-md-12">
                            <h4 className="text-muted"><span className="text-muted mr-3"><i class="fa fa-cogs" aria-hidden="true"></i></span>Architectures</h4>
                            <hr className="mb-5" />
                            <Architectures />
                        </div>
                    </div>
                </div>
            </div>
            <div className="row skills-cst-container">
                <div className="skills-cst shadow">
                    <div className="row">
                        <div className="col-md-12">
                            <h4 className="text-muted"><span className="text-dark mr-3"><i class="fa fa-desktop" aria-hidden="true"></i><i class="fa fa-tablet cst-tablet-font-size" aria-hidden="true"></i></span>Client Side Technologies</h4>
                            <hr className="mb-5" />
                            <ClientSideTechnologies />
                        </div>
                    </div>
                </div>
            </div>
            <div className="row skills-sst-container">
                <div className="skills-sst shadow">
                    <div className="row">
                        <div className="col-md-12">
                            <h4 className="text-muted"><span className="text-danger mr-3"><i class="fa fa-server" aria-hidden="true"></i></span>Server Side Technologies</h4>
                            <hr className="mb-5" />
                            <ServerSideTechnologies />
                        </div>
                    </div>
                </div>
            </div>
            <div className="row skills-st-container">
                <div className="skills-st shadow">
                    <div className="row">
                        <div className="col-md-12">
                            <h4 className="text-muted"><span className="text-info mr-3"><i class="fa fa-database" aria-hidden="true"></i></span>Storage Technologies</h4>
                            <hr className="mb-5" />
                            <StorageTechnologies />
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Skills;