import React from 'react';

import CSharpLogo from '../../assets/pl/C#.jpg';
import PythonLogo from '../../assets/pl/Python.jpg';
import JavaScriptLogo from '../../assets/pl/JavaScript.png';

function ProgrammingLanguages(props) {
    return (
        <div className="card-tech-container">
            <div class="card text-center card-tech">
                <img class="card-img-top" src={CSharpLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">C#<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={PythonLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">Python<span className="text-muted ml-2"></span></p>
                </div>
            </div>
            <div class="card text-center card-tech">
                <img class="card-img-top" src={JavaScriptLogo} alt="React Logo" />
                <div class="card-footer">
                    <p class="card-title card-tech-title">JavaScript<span className="text-muted ml-2"></span></p>
                </div>
            </div>
        </div>
    );
}

export default ProgrammingLanguages;